import { useState, useEffect, useCallback } from 'react';

import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/swiper.min.css';
import 'swiper/components/effect-coverflow/effect-coverflow.min.css';
import 'swiper/components/effect-fade/effect-fade.min.css';

import './styles.css';

import SwiperCore, {
	EffectCoverflow,
	EffectFade,
	Controller
} from 'swiper/core';

import Loading from './Loading';
import Footer from './Footer';
import LogoSection from './LogoSection';
import Navbar from './Navbar';

import categories from './categories';

import ImageViewer from 'react-simple-image-viewer';

SwiperCore.use([EffectCoverflow, EffectFade, Controller]);

export default function App() {
	const [controlledSwiper, setControlledSwiper] = useState(null);
	const [loading, setLoading] = useState(true);
	const [categoryIndex, setCategoryIndex] = useState(0);

	const [currentImage, setCurrentImage] = useState(0);
	const [isViewerOpen, setIsViewerOpen] = useState(false);

	const openImageViewer = useCallback(index => {
		setCurrentImage(index);
		setIsViewerOpen(true);
	}, []);

	const closeImageViewer = () => {
		setCurrentImage(0);
		setIsViewerOpen(false);
	};

	const load = () => {
		setLoading(true);
		setTimeout(() => {
			setLoading(false);
		}, 2000);
	};

	useEffect(() => {
		load();
	}, []);

	const handleCategorySelection = index => {
		load();
		setTimeout(() => {
			setCategoryIndex(index);
		}, 100);
	};

	return (
		<>
			<Loading loading={loading} />
			<Swiper
				className='golapi-swiper-fade'
				id='controller'
				onSwiper={setControlledSwiper}
				spaceBetween={30}
				effect={'fade'}
				allowTouchMove={false}
			>
				{categories[categoryIndex].slides.map((slide, index) => (
					<SwiperSlide key={index}>
						<img alt='slides' src={slide.img} />
					</SwiperSlide>
				))}
			</Swiper>
			<Navbar
				style={isViewerOpen ? { display: 'none' } : {}}
				{...{
					category_titles: categories.map(category => category.title),
					categoryIndex,
					handleCategorySelection
				}}
			/>
			<Swiper
				className='golapi-swiper-coverflow'
				style={isViewerOpen ? { display: 'none' } : {}}
				controller={{ control: controlledSwiper }}
				effect={'coverflow'}
				grabCursor={true}
				centeredSlides={true}
				slidesPerView={'auto'}
				coverflowEffect={{
					rotate: 0,
					stretch: 10,
					depth: 1000,
					modifier: 1,
					slideShadows: true
				}}
			>
				{categories[categoryIndex].slides.map((slide, index) => (
					<SwiperSlide key={index} onClick={() => openImageViewer(index)}>
						<img alt='slides' src={slide.img} />
						<p className='golapi-slide-text'>
							<span>ARTIST</span> {slide.artist}<br />
							<span>POSITIOM</span> {slide.position}
						</p>
					</SwiperSlide>
				))}
			</Swiper>
			<LogoSection style={isViewerOpen ? { display: 'none' } : {}} />
			<Footer style={isViewerOpen ? { display: 'none' } : {}} />
			{isViewerOpen && (
				<ImageViewer
					src={[categories[categoryIndex].slides[currentImage].img]}
					currentIndex={0}
					onClose={closeImageViewer}
					backgroundStyle={{
						backgroundColor: 'rgba(0,0,0,0.5)'
					}}
				/>
			)}
		</>
	);
}
